import Vue from 'vue';
import Router from 'vue-router';
import axios from 'axios';
import store from '../store';

import {
        CompanyProfile,

        ProductList,
        ProductDetail,
        ProductReport,

        NewsList,
        NewsDetail,

        NoticeList,
        NoticeDetail,

        VoteList,
        VoteDetail,
        VoteStatistics,

        SurveyList,
        SurveyDetail
} from './modules.js';

Vue.use(Router);

const userInfo = 'aid/:aid';

const router = new Router({
  routes: [
    // 公司
    {
      path: `/company/${userInfo}`,
      name: 'Company',
      component: CompanyProfile
    },
    // 产品
    {
      path: `/product/list/${userInfo}`,
      name: 'Product',
      component: ProductList,
      meta: { keepAlive: true }
    },
    {
      path: `/product/:id/detail/${userInfo}`,
      component: ProductDetail,
      children: [
        {
          path: `/product/:id/report/history/${userInfo}`,
          component: ProductReport
        }
      ]
    },
    // 新闻
    {
      path: `/news/list/:type/${userInfo}`,
      component: NewsList,
      meta: { keepAlive: true }
    },
    // flag 1正式 0 预览
    {
      path: `/news/:id/detail/:flag/${userInfo}`,
      component: NewsDetail
    },
    // 通知
    {
      path: `/notice/list/${userInfo}`,
      component: NoticeList,
      meta: { keepAlive: true }
    },
    {
      path: `/notice/:id/detail/:flag/:type/${userInfo}`,
      component: NoticeDetail
    },
    // 投票
    {
      path: `/vote/list/${userInfo}`,
      component: VoteList,
      meta: { keepAlive: true }
    },
    {
      path: `/vote/:id/detail/${userInfo}`,
      component: VoteDetail,
      children: [
        {
          path: 'statistics',
          component: VoteStatistics
        }
      ]
    },
    {
      path: `/vote/:id/detail/${userInfo}/admin`, // 预览
      component: VoteDetail,
      meta: { preview: true }
    },
    // 调研
    {
      path: `/survey/list/${userInfo}`,
      component: SurveyList,
      meta: { keepAlive: true }
    },
    {
      path: `/survey/:id/detail/${userInfo}`,
      component: SurveyDetail
    },
    {
      path: `/survey/:id/detail/${userInfo}/admin`,
      component: SurveyDetail,
      meta: { preview: true }
    }
  ]
});
// 全局前置守卫
router.beforeEach((to, from, next) => {
  store.commit('UPDATE_LOADING', true);
  if (process.env.NODE_ENV === 'production') {
    checkLogin(to, from, next);
  } else {
    next();
  }
});

router.afterEach(function(to) {
  setTimeout(function() {
    store.commit('UPDATE_LOADING', false);
  }, 100);
});

// 检查是否登录
function checkLogin(to, from, next) {
  let aid = to.params.aid;
  let url = encodeURIComponent(window.location.href);
  axios
    .post('/weixin/user/isLogin')
    .then(res => {
      if (res.data.code === 402) {
        axios
          .post('/weixin/user/login/aid/' + aid + '/callback/' + url)
          .then(res => {
            if (res.data.code === 200) {
              window.location = res.data.data.url;
            }
          })
          .catch(err => {
            console.error(err);
          });
      } else if (res.data.code === 200) {
        next();
      }
    })
    .catch(err => {
      alert(err);
      console.error(err);
    });
  // document.title = title;
  next();
}
export default router;



