import storage from 'good-storage';

/**
 * global
 */
const USER_INFO_KEY = '__user_info__';

export function saveUserInfo(userInfo) {
  const oldInfo = storage.get(USER_INFO_KEY, {});

  storage.set(USER_INFO_KEY, Object.assign({}, oldInfo, userInfo));

  return userInfo;
}

export function loadUserInfo() {
  return storage.get(USER_INFO_KEY, {});
}

/**
 * vote
 */
const VOTE_LIST_KEY = '__vote_list__';
const VOTE_KEY = '__vote__';
const JOIN_COUNT_KEY = '__join_member__';
const VOTE_COUNT_KEY = '__vote_count__';

export function saveVoteList(list) {
  storage.set(VOTE_LIST_KEY, list);
  return list;
}

export function saveVote(vote) {
  storage.set(VOTE_KEY, vote);
  return vote;
}

export function saveJoinCount(member) {
  storage.set(JOIN_COUNT_KEY, member);
  return member;
}
// 允许多次投票后端未给出已投次数，前端保存投票次数
export function saveVoteCount(voteCount) {
  storage.set(VOTE_COUNT_KEY, voteCount);
}

export function loadVoteList() {
  return storage.get(VOTE_LIST_KEY, []);
}

export function loadVote() {
  return storage.get(VOTE_KEY, {});
}

export function loadJoinCount() {
  return storage.get(JOIN_COUNT_KEY, {});
}
// 允许多次投票后端未给出已投次数，前端保存投票次数
export function loadVoteCount() {
  return storage.get(VOTE_COUNT_KEY, {});
}

/**
 * survey
 */
const SURVEY_LIST_KEY = '__survey_list__';
const SURVEY_KEY = '__survey__';

export function saveSurveyList(list) {
  storage.set(SURVEY_LIST_KEY, list);
  return list;
}

export function saveSurvey(survey) {
  storage.set(SURVEY_KEY, survey);
  return survey;
}

export function loadSurveyList() {
  return storage.get(SURVEY_LIST_KEY, []);
}

export function loadSurvey() {
  return storage.get(SURVEY_KEY, {});
}



