<template>
  <div id="app">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive" />
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive" />
    <loading :show="isLoading" />
  </div>
</template>
<script>
import Loading from '@/base/toast/loading';
import { mapGetters } from 'vuex';

export default {
  name: 'app',
  computed: {
    ...mapGetters(['isLoading'])
  },
  components: {
    Loading
  }
};
</script>

<style lang="stylus">
@import "~@/common/stylus/index.styl";

body, html
  word-break break-all
</style>



