import * as types from './mutation-types';
import { saveUserInfo, saveVoteList, saveJoinCount, saveSurveyList } from '@/common/js/cache';

const mutations = {
  /**
   * global
   */
  [types.UPDATE_LOADING](state, isLoading) {
    state.isLoading = isLoading;
  },

  [types.SET_USER_TYPE](state, { isAdmin }) {
    saveUserInfo({ isAdmin });
    state.isAdmin = isAdmin;
  },

  [types.SET_USER_ID](state, { id }) {
    saveUserInfo({ id });
    state.userId = id;
  },
  /**
   * vote
   */
  [types.SET_VOTE_LIST](state, voteList) {
    state.voteList = saveVoteList(voteList);
  },
  // [types.SET_VOTE_ITEM](state, vote) {
  //   state.vote = saveVote(vote);
  // },
  // 投票后更新列表的状态
  [types.UPDATE_VOTE_STATUS](state, { voteId, status }) {
    const list = state.voteList;
    for (let i = 0; i < list.length; i++) {
      if (list[i].id === voteId) {
        list[i].status = status;
        break;
      }
    }
  },
  [types.SET_JOIN_COUNT](state, data) {
    state.joinCount = saveJoinCount(data);
  },
   /**
   * survey
   */
  [types.SET_SURVEY_LIST](state, surveyList) {
    state.surveyList = saveSurveyList(surveyList);
  },
  // [types.SET_SURVEY_ITEM](state, survey) {
  //   state.survey = saveSurvey(survey);
  // },
  [types.UPDATE_SURVEY_STATUS](state, { surveyId, status }) {
    const list = state.surveyList;
    for (let i = 0; i < list.length; i++) {
      if (list[i].id === surveyId) {
        list[i].status = status;
        break;
      }
    }
  }
};

export default mutations;



