import { loadUserInfo, loadVoteList, loadJoinCount, loadSurveyList } from '@/common/js/cache';

const state = {
  isLoading: false, // 路由正在跳转

  isAdmin: loadUserInfo().isAdmin, // 身份类别
  userId: loadUserInfo().id, // 用户id

  voteList: loadVoteList(), // 投票列表
  // vote: loadVote(),   // 当前投票项
  joinCount: loadJoinCount(), // 当前投票参与人员

  surveyList: loadSurveyList() // 调研列表
  // survey: loadSurvey() // 当前调研项
};

export default state;



